/* eslint-disable max-lines */
import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import TabsPanels from './TabsPanels';
import TabBar from './TabBar';
import SubTabBar from './SubTabBar';
import { TabTypes } from './TabTypes';

const blockName = 'tabs';

const TabsContainer = aggregatedClasses('div')(blockName);
const TabBarContainer = aggregatedClasses('div')(
  blockName,
  'tab-bar-container',
);

const getDefaultActiveTab = (children) => children && children[0].props.tabId;

const getTabBarByType = (type) =>
  ({
    [TabTypes.NORMAL]: TabBar,
    [TabTypes.SUBTABS]: SubTabBar,
  }[type] || TabBar);

const TabsImpl = ({
  animated,
  children,
  type,
  tabBarExtraContent,
  onTabChange,
  enableMouseEvents,
  innerRef,
  onlyRenderActiveTab,
  containerProps,
  allowTextSelection,
  tabsListAriaLabel,
  activeTab,
  firstSubtabRef,
  lastTabRef,
  withCarousel,
}) => {
  const [actualActiveTab, setActualActiveTab] = useState(
    activeTab || getDefaultActiveTab(children),
  );
  const firstTabRefByParent = useRef(null);
  const lastTabRefByParent = useRef(null);
  const TabBarComponent = getTabBarByType(type);

  useEffect(() => {
    if (activeTab) {
      setActualActiveTab(activeTab);
    }
  }, [activeTab]);

  const tabs = children;

  const handleTabClick = (tab) => {
    setActualActiveTab(tab);
    onTabChange(tab);
  };

  const handleDownArrow = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (firstTabRefByParent?.current) firstTabRefByParent.current?.focus();
  };

  const handleUpArrow = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (lastTabRef?.current) lastTabRef.current?.focus();
  };

  return (
    <TabsContainer
      {...containerProps}
      aria-label={tabsListAriaLabel}
      innerRef={innerRef}
      role="tablist"
    >
      <TabBarContainer>
        <TabBarComponent
          withCarousel={withCarousel}
          activeTab={actualActiveTab}
          onTabClick={handleTabClick}
          tabs={tabs}
          firstSubtabRef={firstSubtabRef}
          lastTabRef={lastTabRefByParent}
          handleDownArrow={handleDownArrow}
          handleUpArrow={handleUpArrow}
        />
        {tabBarExtraContent}
      </TabBarContainer>
      <TabsPanels
        activeTab={actualActiveTab}
        allowTextSelection={allowTextSelection}
        animated={animated}
        enableMouseEvents={enableMouseEvents}
        onlyRenderActiveTab={onlyRenderActiveTab}
        onTabChange={handleTabClick}
        tabsComponents={tabs}
        lastTabRef={lastTabRefByParent}
        firstTabRefByParent={firstTabRefByParent}
      />
    </TabsContainer>
  );
};

TabsImpl.propTypes = {
  animated: PropTypes.bool,
  enableMouseEvents: PropTypes.bool,
  allowTextSelection: PropTypes.bool,
  onTabChange: PropTypes.func,
  type: PropTypes.oneOf(['normal', 'subtabs']),
  tabBarExtraContent: PropTypes.element,
  onlyRenderActiveTab: PropTypes.bool,
  withCarousel: PropTypes.bool,
  activeTab: PropTypes.string,
  children: PropTypes.arrayOf(
    PropTypes.shape({
      type: PropTypes.any,
    }),
  ).isRequired,
  tabsListAriaLabel: PropTypes.string,
  containerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any }),
  ]),
  firstSubtabRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any }),
  ]),
  lastTabRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any }),
  ]),
};

export default TabsImpl;
