import { useLayoutEffect } from 'react';
import { debounce } from 'lodash';

export function useResize(ref, callback) {
  const { right, left } = ref?.current?.getBoundingClientRect() || {
    right: null,
    left: null,
  };
  const handleResize = debounce(callback, 300);

  useLayoutEffect(() => {
    handleResize();
  }, [right, left]);
}
