import React from 'react';
import PropTypes from 'prop-types';
import { aggregatedClasses } from '@elliemae/ds-classnames';
const blockName = 'tabs-panels';

const PC = aggregatedClasses('div')(blockName, 'panel');

const PanelContainer = ({ children, lastTabRef, firstSubtabRef, ...rest }) => {
  // inject ref to handle focus on first subtab child
  const child = React.Children.map(children, (c, i) =>
    i === 0 && c ? React.cloneElement(c, { firstSubtabRef, lastTabRef }) : c,
  );
  return <PC {...rest}>{child}</PC>;
};

PanelContainer.propTypes = {
  children: PropTypes.any,
  firstSubtabRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any }),
  ]),
  lastTabRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any }),
  ]),
};

export default PanelContainer;
